.container{

    width: 50%;
    padding: 0.2em;
}
.video{
    display: flex;
    align-items: center;
    border: 1px solid lightgray;
    margin-bottom: 0.2em;
    box-shadow: 3px 3px 5px 0px rgba(191, 191, 191, 0.53);
    cursor: pointer;
    transition:  transform 250ms ease-in;
}
.video:hover{
    transform: scale(1.02);
}

.metadata{
    margin-left: 0.2em;
}
.thumbnail{
   width: 40%;
   height: 100%;
}
.title, .channel{
    margin: 0;
    font-size: 0.8rem;
}
.channel{
    font-size: 0.6rem;
}